<template>
    <div>
        <div class="loading-container" v-show="loading">
            <loading></loading>
        </div>
        <div class="container">
            <div
                class="book"
                ref="wrapper"
                @click="setRead($event)"
                :style="{color: fontColor, background: background}"
            >
                <cube-scroll
                    ref="scroll"
                    :data="pattern"
                    v-if="pagingPattern===0"
                    class="books-con"
                >
                    <div class="book-content" ref="content">
                        <h1
                            class="book-title"
                            :style="{fontSize: fontSize*1.2+'px'}"
                        >{{bookContent.title}}</h1>
                        <div
                            class="book-inner"
                            v-html="currentArticle"
                            :style="{fontSize: fontSize+'px'}"
                        ></div>
                        <div
                            class="load-next"
                            @click.stop="nextChapter"
                            v-show="isMoreLoading"
                        >加载下一章</div>
                    </div>
                </cube-scroll>
                <div class="read-content" v-if="pagingPattern===1">
                    <p class="book-title" style="padding: 10px">{{bookContent.title}}</p>
                    <div class="read-article">
                        <section class="read-section" :style="styleObject">
                            <div
                                class="read-section-inner"
                                :style="{columns:clWidth-20+'px',columnGap:10+'px'}"
                                ref="bookInner"
                                id="bookInner"
                            >
                                <h1
                                    class="book-title"
                                    :style="{fontSize:fontSize*1.2+'px'}"
                                >{{bookContent.title}}</h1>
                                <div
                                    class="book-inner"
                                    :style="{fontSize:fontSize+'px'}"
                                    v-html="currentArticle"
                                ></div>
                            </div>
                        </section>
                    </div>
                    <p class="article-page">{{currentPaging + '/' + resultPaging}}</p>
                </div>
            </div>
            <div class="page-read-option" :class="{'show-panel':option}" @click="setReadShow()">
                <div class="top-controll-bar">
                    <div class="header">
                        <div class="back" @click="$router.back(-1)">
                            <i class="icon-arrow icon-arrow-l"></i>
                        </div>
                        <div class="title">书籍目录</div>
                        <div class="sort"></div>
                    </div>
                </div>
                <div class="addReader">
                    <p>{{flag ? '撤出书架' : '加入书架'}}</p>
                </div>

                <div class="bottom-controll-bar">
                    <div class="readSet">
                        <div class="readSet-item">
                            <span @click.stop="fontSet('reduce',$event)">Aa-</span>
                            <span @click.stop="fontSet('add',$event)">Aa+</span>
                        </div>
                        <div class="readSet-item pattern">
                            <span
                                v-for="(item,index) in pattern"
                                :key="index"
                                :class="{active:readePattern===index}"
                                @click.stop="setPattern(item,index)"
                            >{{item.item}}</span>
                        </div>
                        <div class="readSet-item">
                            <span
                                @click.stop="setPaging(0)"
                                :class="{active:pagingPattern===0}"
                            >上下滑动</span>
                            <span
                                @click.stop="setPaging(1)"
                                :class="{active:pagingPattern===1}"
                            >左右滑动</span>
                        </div>
                    </div>
                    <cube-button type="default" @click="prevChapter" style="box-shadow:none">上一章</cube-button>
                    <cube-button type="primary" @click.stop="setList" style="box-shadow:none">目录</cube-button>
                    <cube-button type="default" @click="nextChapter" style="box-shadow:none">下一章</cube-button>
                </div>
            </div>
            <div class="chapter-list"
                @click.stop="setList"
                v-show="listOption">
                <div class="chapter-main" :style="{transform: 'translate3d('+chapterWidth+', 0px, 0px)'}">
                    <div class="chapter-header">
                        <div class="title">书籍目录</div>
                        <div class="sort" @click.stop="changeOrder()">{{orderBy? '倒序': '正序'}}</div>
                    </div>
                    <cube-scroll
                        ref="chapters"
                        :data="chapters"
                        :options="options"
                        @pulling-up="onPullingUp"
                        class="list-wrap"
                    >
                        <ul>
                            <li v-for="chapter in chapters" :key="chapter._id">
                                <a class="bar" href="javascript:;" v-if="chapter.chapter_level==1">
                                    <span>{{chapter.chapter_name}}</span>
                                </a>
                                <a
                                    class="idx"
                                    href="javascript:;"
                                    @click="getChapter(chapter._id)"
                                    v-else
                                >
                                    <span>{{chapter.chapter_name}}</span>
                                    <i class="icon-arrow"></i>
                                </a>
                            </li>
                        </ul>
                    </cube-scroll>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
import Loading from 'components/loading/loading'
export default {
    name: 'Read',
    data() {
        return {
            bookId: 1,
            chapterId: 1,
            bookContent: {
                title: '', // 章节名称
                content: '内容加载中...', // level==1 卷首，返回内容：第一卷; level==2为正文，返回内容：内容oss
                preId: 0, // 上一章id，为0时表示第一章
                nextId: 0, // 下一章id，为0时表示最后一章
                level: 2, // 1卷名，2 章节
                chapterId: 1 // 当前章节id
            },
            nextContent: {
                title: '', // 章节名称
                content: '内容加载中...', // level==1 卷首，返回内容：第一卷; level==2为正文，返回内容：内容oss
                preId: 0, // 上一章id，为0时表示第一章
                nextId: 0, // 下一章id，为0时表示最后一章
                level: 2, // 1卷名，2 章节
                chapterId: 1 // 当前章节id
            },
            prevContent: {
                title: '', // 章节名称
                content: '内容加载中...', // level==1 卷首，返回内容：第一卷; level==2为正文，返回内容：内容oss
                preId: 0, // 上一章id，为0时表示第一章
                nextId: 0, // 下一章id，为0时表示最后一章
                level: 2, // 1卷名，2 章节
                chapterId: 1 // 当前章节id
            },
            bookCurrentPage: 0,
            currentArticle: '',
            bookInfo: {},
            chapters: [],
            pattern: [
                {
                    item: '默认',
                    color: '#5c5d58',
                    background: '#eee6dd'
                },
                {
                    item: '夜间',
                    color: '#666',
                    background: '#000'
                },
                {
                    item: '护眼',
                    color: '#5c5d58',
                    background: '#c8e8c8'
                }
            ],
            offsetX: 0,
            margin: 10,
            currentPaging: 1,
            resultPaging: 1,
            prevResultPaging: 1,
            styleObject: '',
            isClickPage: false,
            fontSize: 18,
            fontColor: '#5c5d58',
            background: '#eee6dd',
            chapterWidth: 0,
            readePattern: 0,
            pagingPattern: 0,
            bootPage: 0,
            clWidth:
                document.documentElement.clientWidth ||
                document.body.clientWidth,
            option: false,
            listOption: false,
            flag: false,
            loading: false,
            isMoreLoading: false,
            orderBy: 1, //默认1 正序
            pageIndex: 1,
            isShowMore: false, // 是否获取到更多数据
            pullUpLoad: true,
            pullUpLoadThreshold: 0,
            pullUpLoadMoreTxt: '加载中...',
            pullUpLoadNoMoreTxt: '没有更多数据啦'
        }
    },
    created() {
        console.log('pagingPattern:'+this.pagingPattern)
        this.bookId = this.$route.params.id
        this.chapterId = this.$route.params.chapter
        this.bookContent.chapterId = this.chapterId
        this.getNewUser()
        this.getBookDetail()
        this.getBookChapter()
    },
    mounted() {
        this.getUserLocalStorage()
        const that = this
        window.onresize = () => {
            return (() => {
                that.clWidth = document.body.clientWidth
                that.$router.go(0)
            })()
        }
        this.$nextTick(()=>{
            this.$refs.chapters.refresh() 
        })
    },
    computed: {
        options() {
            return {
                pullUpLoad: this.pullUpLoadObj
            }
        },
        pullUpLoadObj: function() {
            return this.pullUpLoad
                ? {
                      threshold: parseInt(this.pullUpLoadThreshold),
                      txt: {
                          more: this.pullUpLoadMoreTxt,
                          noMore: this.pullUpLoadNoMoreTxt
                      }
                  }
                : false
        }
    },
    methods: {
        //获取是否为新用户，新用户的则需要引导页
        getNewUser() {
            let localShelf = JSON.parse(localStorage.getItem('bootPage'))
                ? JSON.parse(localStorage.getItem('bootPage'))
                : {}
            if (localShelf.bootPage) {
                this.bootPage = localShelf.bootPage
            } else {
                this.bootPage = 0
            }
        },
        //引导页
        setBootPage() {
            let localShelf = JSON.parse(localStorage.getItem('bootPage'))
                ? JSON.parse(localStorage.getItem('bootPage'))
                : {}
            localShelf = {
                bootPage: 1
            }
            this.bootPage = 1
            localStorage.setItem('bootPage', JSON.stringify(localShelf))
        },
        //获取用户设置
        getUserLocalStorage() {
            let localShelf = JSON.parse(localStorage.getItem('userSet'))
                ? JSON.parse(localStorage.getItem('userSet'))
                : {}
            this.fontSize = localShelf.fontSize ? localShelf.fontSize: this.fontSize
            this.background = localShelf.background? localShelf.background: this.background
            this.fontColor = localShelf.color? localShelf.color: this.fontColor
            this.readePattern = localShelf.readePattern? localShelf.readePattern: this.readePattern
            this.pagingPattern = localShelf.pagingPattern? localShelf.pagingPattern: this.pagingPattern
        },
        //将用户设置进行缓存
        setUserLocalStorage() {
            let localShelf = JSON.parse(localStorage.getItem('userSet'))
                ? JSON.parse(localStorage.getItem('userSet'))
                : {}
            localShelf = {
                fontSize: this.fontSize,
                background: this.background,
                color: this.fontColor,
                readePattern: this.readePattern,
                pagingPattern: this.pagingPattern
            }
            localStorage.setItem('userSet', JSON.stringify(localShelf))
        },
        getBookDetail() {
            let data = {
                nid: this.bookId
            }
            this.$post('/novel/detail', data).then(res => {
                // console.log(res)
                if (res.code === 10000) {
                    let result = res.result
                    this.bookInfo = {
                        book_cover: result.book_cover,
                        book_name: result.book_name,
                        author_name: result.author_name,
                        category_name: result.category_name,
                        book_id: result.book_id,
                        book_from_id: result.book_from_id,
                        chapter_list: result.chapter_list
                    }
                    console.log(this.bookInfo)
                    console.log(this.chapterId)
                    this.getChapter(this.chapterId)
                }
            })
        },
        async getContent(id) {
            console.log(this.chapterId)
            console.log(this.bookContent.chapterId)
            let cid = id ? id : this.bookContent.chapterId
            let data = {
                book_from_id: this.bookInfo.book_from_id,
                book_id: this.bookId,
                chapter_id: cid
            }
            return new Promise((resolve, reject) => {
                // this.$post('/content/read', data)
                this.$post('/chapter/content', data)
                    .then(res => {
                        // console.log(res)
                        if (res.code === 10000) {
                            let result = res.result
                            let _content = {
                                title: result.name,
                                content: result.content,
                                preId: result.pre_id,
                                nextId: result.next_id,
                                level: result.level,
                                chapterId: cid
                            }
                            resolve(_content)
                        }
                    })
                    .catch(err => {
                        reject(err)
                    })
            })
        },
        // 获取当前章节内容
        async getChapter(id) {
            this.bookContent = await this.getContent(id)
            console.log(this.bookContent)
            this.downContent(this.bookContent.content)
            this.addReadHistory()
        },
        async getPrevChapter(click) {
            this.prevContent = await this.getContent(this.bookContent.preId)
            if (click) {
                this.bookContent = this.prevContent
                this.downContent(this.bookContent.content)
                this.addReadHistory()
            }
        },
        async getNextChapter(click) {
            this.nextContent = await this.getContent(this.bookContent.nextId)
            if (click) {
                this.bookContent = this.nextContent
                this.downContent(this.bookContent.content)
                this.addReadHistory()
            }
        },
        //上下章节切换
        nextChapter() {
            if (this.bookContent.nextId === 0) {
                this.$createToast({
                    time: 2000,
                    txt: '已经是最后一章了！',
                    type: 'text',
                    mask: true
                }).show()
                return
            }
            if (this.pagingPattern === 0) {
                this.isMoreLoading = false
            }
            this.loading = true
            this.getNextChapter('click')
            this.currentPaging = 1
            this.offsetX = 0
            this.styleObject = ''
            this.bookCurrentPage++
            if (this.getBookLocalStorage()[this.$route.params.id]) {
                this.setBookLocalStorage()
            }
        },
        prevChapter() {
            if (this.bookContent.preId === 0) {
                this.$createToast({
                    time: 2000,
                    txt: '已经是第一章了！',
                    type: 'text',
                    mask: true
                }).show()
                return
            }
            if (this.pagingPattern === 0) {
                this.isMoreLoading = false
            }
            this.getPrevChapter('click')
            this.currentPaging = 1
            this.offsetX = 0
            this.styleObject = ''
            this.bookCurrentPage--
            if (this.getBookLocalStorage()[this.$route.params.id]) {
                this.setBookLocalStorage()
            }
        },
        downContent(downurl) {
            let _content
            this.$fetch(downurl).then(res => {
                this.currentArticle = this.bookChaptersBody(res)
                console.log(this.currentArticle)
            })

            return _content
        },
        //字符串解析
        bookChaptersBody(val) {
            let c = /[\u4E00-\u9FA5\uF900-\uFA2D]/g
            let _book = JSON.stringify(val).replace(/^"|"$/g, '')
            this.loading = false
            if (c.test(_book)) {
                _book = _book
                    .replace(/\s*/g, '')
                    .replace(/([\\r\\n]+)/g, '\n')
                    .replace(/\\n+/g, '\n')
                    .replace(/\n/g, '</p><p>')
                _book = '<p>' + _book + '</p>'
                this.loading = false
                if (this.pagingPattern === 0) {
                    this.$refs.scroll.refresh()
                    this.$refs.scroll.scrollTo(0, 0, 0)
                    setTimeout(() => {
                        this.isMoreLoading = true
                    }, 500)
                }

                return _book
            } else {
                this.loading = false
                return 'vip章节，请购买或者换源阅读！' //换源功能已经实现
            }
        },

        setList() {
            this.listOption = !this.listOption
            if (!this.option) {
                return
            }
            if(this.listOption){
                this.chapterWidth = '-300px'
                setTimeout(()=>{
                    this.$refs.chapters.refresh()
                }, 300)
            }
            this.option = !this.option
        },
        //阅读模式设置
        setPattern(item, index) {
            this.option = true
            this.readePattern = index
            this.fontColor = item.color
            this.background = item.background
            this.setUserLocalStorage()
        },
        //字体设置
        fontSet(str, e) {
            this.option = true
            if (str === 'reduce') {
                if (this.fontSize === 14) {
                    return
                } else {
                    this.fontSize -= 0.8
                    this.setUserLocalStorage()
                }
            }
            if (str === 'add') {
                if (this.fontSize === 22) {
                    return
                } else {
                    this.fontSize += 0.8
                    this.setUserLocalStorage()
                }
            }
        },
        //翻页模式设置
        setPaging(num) {
            this.option = !this.option
            this.pagingPattern = num
            this.setUserLocalStorage()
            if (num === 1) {
                this.$nextTick(function() {
                    let scrollW = this.$refs.bookInner.scrollWidth
                    let clientW = this.$refs.bookInner.clientWidth
                    this.resultPaging = Math.floor(scrollW / clientW)
                })
            } else {
                this.$refs.scroll.refresh()
                this.$refs.scroll.scrollTo(0, 0, 0)
            }
        },
        setReadShow() {
            this.option = !this.option
        },
        setRead(e) {
            if (this.pagingPattern === 0) {
                this.option = !this.option
                return
            }
            let clWidth =
                document.documentElement.clientWidth ||
                document.body.clientWidth
            let currentX = e.pageX
            let offsetX = clWidth / 3

            if (currentX <= offsetX) {
                //用户点击左侧三分之一
                if (this.currentPaging === 1 && this.bookContent.preId === 0) {
                    this.$createToast({
                        time: 2000,
                        txt: '已经是第一章了！',
                        type: 'text',
                        mask: true
                    }).show()
                    return
                }

                if (this.currentPaging === 1) {
                    //当前页为第一页的时候

                    this.isClickPage = true
                    this.getPrevChapter('click')
                } else {
                    //当前页为其他页面的时候
                    this.currentPaging--
                    this.offsetX += clWidth - this.margin
                    this.styleObject = {
                        transform: 'translateX(' + this.offsetX + 'px' + ')',
                        transitionDuration: '0.5s'
                    }
                    if (this.getBookLocalStorage()[this.$route.params.id]) {
                        this.setBookLocalStorage()
                    }
                }
            } else if (offsetX <= currentX && currentX <= offsetX * 2) {
                //点击中间三分之一的时候
                this.option = !this.option
            } else if (currentX >= offsetX * 2 && currentX <= clWidth) {
                //用户点击右侧三分之一
                this.isClickPage = false
                // if (this.bookCurrentPage === this.bookSourceLinks.length) {
                console.log(this.currentPaging, this.resultPaging)
                if (
                    this.currentPaging === this.resultPaging &&
                    this.bookContent.nextId === 0
                ) {
                    this.$createToast({
                        time: 2000,
                        txt: '已经是最后一章了！',
                        type: 'text',
                        mask: true
                    }).show()
                    return
                }
                //翻页
                this.currentPaging++
                if (this.resultPaging - this.currentPaging === 2) {
                    //当前页为总页数的倒数第二页的时候加载下一章
                    this.getNextChapter()
                }
                if (this.currentPaging === this.resultPaging + 1) {
                    //当前页等于总页数的时候
                    // Indicator.open("加载中...");
                    this.styleObject = ''
                    this.offsetX = 0
                    this.getNextChapter('click')
                    this.bookCurrentPage++
                    this.currentPaging = 1
                    if (this.getBookLocalStorage()[this.$route.params.id]) {
                        this.setBookLocalStorage()
                    }
                } else {
                    //当前页不等于总页数的时候
                    this.offsetX -= clWidth - this.margin
                    this.styleObject = {
                        transform: 'translateX(' + this.offsetX + 'px' + ')',
                        transitionDuration: '0.5s'
                    }
                    if (this.getBookLocalStorage()[this.$route.params.id]) {
                        this.setBookLocalStorage()
                    }
                }
            }
        },
        //获取和设置LocalStorage
        getBookLocalStorage() {
            let localShelf = JSON.parse(window.localStorage.getItem('book'))
                ? JSON.parse(window.localStorage.getItem('book'))
                : {}
            if (localShelf[this.$route.params.id]) {
                this.flag = true
            }
            return localShelf
        },
        //将书源和章节及页面缓存到LocalStorage
        setBookLocalStorage() {
            let localShelf = JSON.parse(window.localStorage.getItem('book'))
                ? JSON.parse(window.localStorage.getItem('book'))
                : {}
            localShelf[
                this.$route.params.id
            ].chapterIndexCache = this.bookCurrentPage
            localShelf[this.$route.params.id].bookSource = this.BookSourcesId
            localShelf[
                this.$route.params.id
            ].pageIndexCache = this.currentPaging
            window.localStorage.setItem('book', JSON.stringify(localShelf))
        },
        // 增加阅读记录
        addReadHistory() {
            let data = {
                book_id: this.bookId,
                last_chapter: this.bookContent.chapterId
            }
            this.$post('/user/add_last_chapter', data).then(res => {
                if (res.code === 10000) {
                    console.log('添加记录成功')
                }
            })
        },
        changeOrder(){
            this.orderBy = !this.orderBy
            this.pageIndex = 1
            this.chapters = []
            this.getBookChapter()
        },
        getBookChapter() {
            // 获取书籍
            let data = {
                nid: this.bookId,
                pn: this.pageIndex,
                num: 30,
                order: this.orderBy? 1: 0
            }
            this.$post('/chapter/pages', data).then(res => {
                // console.log(res)
                if (res.code === 10000) {
                    let result = res.result
                    this.chapters = this.chapters.concat(result)

                    this.isShowMore = result && result.length
                    if (this.isShowMore) {
                        //有新数据
                        setTimeout(() => {
                            this.$refs.chapters.refresh()
                        }, 500)
                    } else {
                        const toast = this.$createToast({
                            txt: '暂无更多目录信息',
                            type: 'txt',
                            mask: true,
                            time: 2000
                        })
                        toast.show()
                        this.pullUpLoad = false
                        this.$refs.chapters.forceUpdate()
                    }
                }
            })
        },
        onPullingUp() {
            // 更新数据
            setTimeout(() => {
                this.pageIndex += 1
                this.getBookChapter()
            }, 1000)
        }
    },
    components: {
        Loading
    },
    watch: {
        currentArticle(newVal, oldVla) {
            this.$refs.wrapper.scrollTop = 0
            if (this.pagingPattern === 1) {
                this.$nextTick(function() {
                    let scrollW = this.$refs.bookInner.scrollWidth
                    let clientW = this.$refs.bookInner.clientWidth
                    this.resultPaging = Math.floor(scrollW / clientW)
                    if (this.isClickPage) {
                        this.currentPaging = this.resultPaging
                        this.offsetX =
                            -(this.currentPaging - 1) *
                            (this.clWidth - this.margin)
                        this.styleObject = {
                            transform:
                                'translateX(' + this.offsetX + 'px' + ')',
                            transitionDuration: '0s'
                        }
                        if (this.getBookLocalStorage()[this.$route.params.id]) {
                            this.setBookLocalStorage()
                        }
                    }
                })
            }
        },
        $route(to, from) {
            //监听路由是否变化
            if (to.params.id != from.params.id) {
                this.bookId = to.params.id
                this.getNovelDetail()
                this.chapterId = to.params.chapter
                this.bookContent.chapterId = this.chapterId
                this.getBookDetail()
            }
        }
    }
}
</script>
<style lang="stylus" scoped>
@import 'common/stylus/variable'
@import 'common/stylus/mixin'
.icon-arrow
    border-right 1px solid @color-text-d
    border-top 1px solid @color-text-d
    height 10px
    width 10px
    -webkit-transform rotate(45deg)
    transform rotate(45deg)
    border-color inherit
    border-left none
    border-bottom none
    display inline-block
// 上空心箭头
.icon-arrow-t
    -webkit-transform rotate(-45deg)
    transform rotate(-45deg)
// 下空心箭头
